import React, { Component } from 'react';
import './App.css';

import $ from 'jquery';

import phrases from "./requestData/phrases.json";

class App extends Component {
  constructor(props) {
    super(props);

    this.canvasRef = React.createRef();
    this.imageRef = React.createRef();

    this.state = {
      isLoaded: false,
      imgBinary: ''
    }
  }

  componentDidMount = () => {
    const urlCat = 'https://aws.random.cat/meow';
    $.ajax({
      type: 'GET',
      url: urlCat,
      dataType: 'json',
      success: (data) => {
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const fileUrl = data['file'];
        console.log(`${fileUrl}`) // del
        // `${fileUrl}`
        let xhr = new XMLHttpRequest();
        xhr.open('GET', proxyurl + fileUrl, true);
        xhr.responseType = 'blob';
        xhr.onreadystatechange = () => {
          if (xhr.readyState !== 4) {
            return;
          }
          if (xhr.status === 200) {
            let blob = xhr.response;
            this.setState({
              isLoaded: true,
              imgBinary: blob
            });
          } else if (xhr.status === 499) {
            console.log('retry...') // add
          } else {
            console.log('Error');
          }
        }
        xhr.send();
      },
      error: (data) => {
        console.log('Error in ajax request\n');
        console.log(data)// del
      }
    });
  }

  componentDidUpdate = () => {
    const phrase = phrases[Math.floor(Math.random() * phrases.length)];
    console.log(phrase)// del
    let canvas = this.canvasRef.current;
    let image = this.imageRef.current;
    let ctx = canvas.getContext('2d');
    image.onload = () => {
        const { width, height } = image;
        const mp = 480/height;
        canvas.width = width * mp;
        canvas.height = height * mp;
        ctx.drawImage(image, 0, 0, width * mp, height * mp);
        ctx.textAlign = 'center';
        ctx.strokeAlign = 'center';
        ctx.fillStyle = 'white';
        ctx.font = "40px Lobster";
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 2;
        ctx.strokeText(phrase, width * mp / 2, 480 - 60);
        ctx.fillStyle = 'white';
        ctx.fillText(phrase, width * mp / 2, 480 - 60);
    }
  }

  render() {
    if (!this.state.isLoaded) {
      return(
        <div className="App">
          <h1> ...</h1>
        </div>
      );
    } else {
      return(
        <div className="App">
          <h1> :</h1>
          <canvas ref={this.canvasRef} width={640} height={480} />
          <img ref={this.imageRef} src={URL.createObjectURL(this.state.imgBinary)} alt='' style={{display: 'none'}} />
        </div>
      );
    }
  }
}

export default App;



